<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="showDialog('add')" class="btn-main-deepen">添加类别</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="15%">序号</th>
          <th width="25%">类别名称</th>
          <th width="45%">备注</th>
          <th width="15%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{(key + 1) * table.query.pageNo}}</td>
          <td>{{item.name}}</td>
          <td>{{item.remark}}</td>
          <td>
            <span @click="showDialog('edit',item)" class="oper">编辑</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <el-dialog width="540px" class="dialog" :title="title" :visible.sync="dialog">
        <div class="item">
          <span class="title">类别名称</span>
          <el-input placeholder="请输入类别名称" class="input" v-model="form.name"></el-input>
        </div>
        <div class="item">
          <span class="title">备注</span>
          <el-input placeholder="请输入备注" :rows="5" class="input" type="textarea" v-model="form.remark"></el-input>
        </div>
        <div class="btns">
          <div v-show="title === '添加类别'" @click="addType" class="btn-submit mr15">确定</div>
          <div v-show="title === '编辑类别'" @click="modifyType" class="btn-submit mr15">确定</div>
          <div @click="dialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "档案管理", path: "go(-1)"}, {name: "类别管理", path: ""}],
        title: '添加类别',
        dialog: false,
        searchData: [
          {type: 'input', placeholder: '类别名称'}],
        table:{
          url: '/dossier/queryDossierTypePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            keywords: null
          }
        },
        form: {
          id: '',
          name: '',
          remark: ''
        }
      }
    },
    methods: {
      // 展现弹窗
      showDialog(type,item){
        if(type === 'add'){
          this.title = '添加类别'
          this.dialog = true
          this.form = {
            name: '',
            remark: ''
          }
        }else{
          this.title = '编辑类别'
          this.dialog = true
          this.form = {
            id: item.id,
            name: item.name,
            remark: item.remark
          }
        }

      },
      // 添加类别
      async addType(){
        if(!this.form.name){
          this.$message.warning('请输入类别名称')
          return
        }
        await this.$ajax({
          url: '/dossier/addDossierType.do',
          query:{
            name: this.form.name,
            remark: this.form.remark
          }
        })
        this.$remind('添加成功')
        this.dialog = false
        this.$search(this.table)
      },
      // 修改类别
      async modifyType(){
        if(!this.form.name){
          this.$message.warning('请输入类别名称')
          return
        }
        await this.$ajax({
          url: '/dossier/updateDossierType.do',
          query:{
            id: this.form.id,
            name: this.form.name,
            remark: this.form.remark
          }
        })
        this.$remind('修改成功')
        this.dialog = false
        this.$search(this.table)
      }
    },
    mounted(){
      this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 70px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 70px)
    .item
      margin-bottom: 20px
    .btns
      text-align: center

</style>
